	module.exports = {"data":{"site":{"siteMetadata":{"title":"Apifarms","author":"ApiFarms"}},"markdownRemark":{"id":"/Users/Alex-TBS/Documents/code/bee-project/src/pages/host-a-hive/old-shit.md absPath of file >>> MarkdownRemark","html":"<h2>Thinking of getting a hive with us?</h2>\n<p>Thinking of getting a hive with us? Below are some of the steps we would go through in a year.</p>\n<p>The first step is to send us an inquiry via telephone or email, with information regarding where you would like the hive to be placed. We will send a member of the team to inspect the location to ensure it is a healthy location for the hive. Once everything is confirmed, the rental of the hive begins in late May and early June. The start date of the rental hive is all weather dependentas bees rely on sunny days for foraging.</p>\n<p>When the rental starts, the hive will be in place. The hives will come already registered with OMAFRA.We will then care for the bees, inspecting and maintaining regularly and as needed. As the hive grows we expand the hive by adding supers, or until it’s time for us to harvest your honey. Once harvested, wegive back to the community 70% of the yield. If it happens to be a terrible harvest year no worries; we will guarantee to supply you with some local honey.</p>\n<p>Then comes the time to prepare the hive for winter. Once the hive is ready and packed with food for them to survive the winter, the inspecting apiarist will seal the hive and come back during the winter months to confirm good hive ventilation. The season comes to an end once spring is back in the air, we would then performing our spring inspection. </p>\n<p>For ApiFarms to well maintain a hive customers can expect to see us twice or more on a monthly basis, but no worries since we can tend to the hives even if you are gone on a trip.</p>\n<p>Please feel free to <a href=\"./contact\">contact us</a> if you have any questions.</p>","frontmatter":{"title":"","date":null,"exclude_hr":null,"exclude":null,"image":null}}},"pathContext":{"slug":"/host-a-hive/old-shit/"}}

